import request from "./axios.config";
//api
export default {
  /**
   * @description: 
   * @param { username, password }
   * @return: Promise
   */
  login(params) {
    return request({
      url: "/api/index/login",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 
   * @param {content} 
   * @return: 
   */
  addComment(params) {
    return request({
      url: "/api/index/addComment",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 
   * @param {start} 
   * @return: 
   */
  getCommentList(params) {
    return request({
      url: "/api/index/getCommentList",
      method: "post",
      data: params
    });
  },
  /**
   * @description: 
   * @param {start} 
   * @return: 
   */
  getMyLeaverMessageList(params) {
    return request({
      url: "/api/index/getMyLeaverMessageList",
      method: "post",
      data: params
    });
  },
   /**
   * @description: 
   * @param {idisFlower} 
   * @return: 
   */
  praise(params) {
    return request({
      url: "/api/index/praise",
      method: "post",
      data: params
    });
  }
};
