import axios from "axios";
import md5 from "./md5";
import store from "@/store";
import qs from "qs";
import { Toast } from "vant";

const instance = axios.create({
  baseURL: "http://leslie.markcolin.cn", //URL,api
  timeout: 5000, //
  headers: {
    "X-Requested-With": "XMLHttpRequest",
    "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
  }, //

  responseType: "json" //  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  //  params: {},  //paramskey=valueurl
  //  proxy: {
  //    //
  //     host: '127.0.0.1',
  //     port: 9000,
  //     auth: {
  //       username: 'mikeymike',
  //       password: 'rapunz3l'
  //     }
  // },
});

// 
instance.interceptors.request.use(
  function(config) {
    // 
    const { uid, token } = store.state;
    const timestamp = Math.round(new Date().getTime() / 1000).toString();
    const sign = `uid=${uid}&timestamp=${timestamp}&token=${token}`;

    let customHeader = { timestamp, uid, sign: md5(sign) };
    Object.assign(config.headers, customHeader);

    if (config.method == "POST" || config.method == "post") {
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  function(error) {
    // 
    // Message.error('')
    return Promise.reject(error);
  }
);

// 
instance.interceptors.response.use(
  function(response) {
    // 
    if (response.status === 200 && response.data.code === 1) {
      return response.data.data;
    } else {
      Toast(response.data.msg);
      return Promise.reject(response);
    }
  },
  function(error) {
    // 
    // Toast(error.data.msg);
    console.log(error);
    return Promise.reject(error);
  }
);

//
export default instance;
