import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import { imgsPreloader } from "./utils/imgPreloader.js";
import imgPreloaderList from "./utils/imgPreloaderList.js";

import "@/utils/rem.js";
import "@/utils/reset-mobile.css";

Vue.config.productionTip = false;

(async () => {
  await imgsPreloader(imgPreloaderList);
  //
  document.querySelector(".loading").style.display = "none";
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
})();

//
router.beforeEach((to, from, next) => {
  if (store.state.uid && store.state.token) {
    // console.log(to.path) //
    if (to.path === "/") {
      // login.vue  swipers.vue
      next({ path: "/swipers" });
    } else {
      next();
    }
  } else {
    // session , 
    if (to.path === "/") {
      // next() -->bug
      next();
    } else {
      //  
      next({ path: "/" });
    }
  }
});
